import React, {
    useEffect,
    useState
} from 'react';
// import PropTypes from 'prop-types'
//import Youtube from '../fetchApi/Youtube';

import styled, { keyframes } from 'styled-components';
import { devices } from '../utils/constantes';
import {
    getVideoDetails,
    getPopularVideos,
    searchVideos
} from '../fetchApi/ServiceYoutube';

// Comomponents commons
import Loading from '../components/common/load/Loading';
import ErrorCompoment from '../components/common/error/ErrorComponent';
import VideoDetails from '../components/video/VideoDetails';
import VideoList from '../components/video/VideoList';

const Show = keyframes`
    0%{
        opacity:0;
    }
    50%{
        opacity:0.5;
    }

    100%{
        opacity:1;
    }
`;

const ContainerLoading = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 15rem;

    @media only screen and (${devices.mobileG}) {
        margin: 5rem 1rem;
    }

    @media only screen and (${devices.tablet}) {
        margin: 8rem 2rem;
    }

    @media only screen and (max-width: 600px) {
        margin: 5rem 1rem;
    }
`;

const ContainerError = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 15rem;

    @media only screen and (${devices.mobileG}) {
        margin: 5rem 1rem;
    }

    @media only screen and (${devices.tablet}) {
        margin: 8rem 2rem;
    }

    @media only screen and (max-width: 600px) {
        margin: 5rem 1rem;
    }
`;


const StyleMain = styled.div`
    background: var(--bg-color);
    min-height: 90vh !important;
    padding: 5rem 5rem 0 5rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    margin-bottom: 2rem;
    animation: ${Show} 1s ease-in;

    @media only screen and (${devices.portatilS}) {
        padding: 2rem !important;
    }
    @media only screen and (${devices.portatil}) {
        padding: 2rem !important;
    }

    @media only screen and (${devices.tablet}) {
        flex-direction: column !important;
        padding: 0.9rem !important;
    }

    @media only screen and (max-width: 600px) {
        flex-direction: column !important;
        padding: 0.7rem !important;
    }

    @media only screen and (max-width: 500px) {
        flex-direction: column !important;
        padding: 0.7rem !important;
    }

    @media only screen and (${devices.iphone14}) {
        flex-direction: column !important;
        padding: 0.7rem !important;
    }
    @media only screen and (${devices.mobileG}) {
        flex-direction: column !important;
        padding: 0.7rem !important;
    }
`;

const LeftCol = styled.div`
    flex: 0.7;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 2rem 0.8rem 0.1rem 0.8rem;

    @media only screen and (${devices.tablet}) {
        flex: 2;
    }
`;

// Right col
const RightCol = styled.div`
    flex: 0.4;
    border-radius: 1rem;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 3rem;
    background: var(--dark-color);

    overflow: scroll;

    overflow-x: hidden;
    height: 80vh;
    overflow-y: scroll;
`;

const WatchLaterTitle = styled.h5`
    color: var(--white-color);
    margin-bottom: 1rem;
    text-align: left;
`;

const Main = ({ searchQuery }) => {
    // Estados para armazenar videos populares (watch later)
    const [
        watchLaterVideos,
        setWatchLaterVideos
    ] = useState([]);
    // estado para armazenar o video principal atual
    const [currentVideo, setCurrentVideo] =
        useState(null);

    // estado para controlar o loading
    const [loading, setLoading] = useState(true);

    // estado para controlar erros
    const [error, setError] = useState(null);

    // Adicionar este estado para manter a ltima pesquisa
    const [lastSearchTerm, setLastSearchTerm] =
        useState('');

    // IMPLEMENTAO NOVA: Funo simplificada para garantir persistncia aps refresh
    useEffect(() => {
        const loadStateAfterRefresh = async () => {
            try {
                setLoading(true);
                console.log("Iniciando carregamento de estado aps refresh");
                
                // 1. Tentar carregar o vdeo salvo
                const savedVideoJSON = localStorage.getItem('currentVideoDetails');
                let videoLoaded = false;
                
                if (savedVideoJSON) {
                    try {
                        const savedVideo = JSON.parse(savedVideoJSON);
                        setCurrentVideo(savedVideo);
                        videoLoaded = true;
                        console.log("Vdeo carregado do localStorage:", savedVideo.id);
                    } catch (e) {
                        console.error("Erro ao carregar vdeo:", e);
                    }
                }
                
                // 2. Tentar carregar a lista de vdeos salva
                const savedVideosJSON = localStorage.getItem('watchLaterVideos');
                let videosLoaded = false;
                
                if (savedVideosJSON) {
                    try {
                        const savedVideos = JSON.parse(savedVideosJSON);
                        if (savedVideos && savedVideos.length > 0) {
                            setWatchLaterVideos(savedVideos);
                            videosLoaded = true;
                            console.log("Lista de vdeos carregada com sucesso:", savedVideos.length);
                        }
                    } catch (e) {
                        console.error("Erro ao carregar lista de vdeos:", e);
                    }
                }
                
                // 3. Tentar carregar o termo de pesquisa
                const savedSearch = localStorage.getItem('lastSearchQuery');
                if (savedSearch) {
                    setLastSearchTerm(savedSearch);
                    console.log("Termo de pesquisa carregado:", savedSearch);
                }
                
                // 4. Se no foi possvel carregar do localStorage, carrega vdeos populares (somente primeira visita)
                if (!videosLoaded) {
                    console.log("Carregando vdeos populares (primeira visita)");
                    const videos = await getPopularVideos(15);
                    setWatchLaterVideos(videos);
                    localStorage.setItem('watchLaterVideos', JSON.stringify(videos));
                    
                    // Se no carregou vdeo, usar o primeiro dos populares
                    if (!videoLoaded && videos && videos.length > 0) {
                        try {
                            const firstVideoId = getVideoIdFromResult(videos[0]);
                            if (firstVideoId) {
                                const videoDetails = await getVideoDetails(firstVideoId);
                                setCurrentVideo(videoDetails);
                                localStorage.setItem('currentVideoDetails', JSON.stringify(videoDetails));
                            }
                        } catch (e) {
                            console.error("Erro ao carregar primeiro vdeo:", e);
                        }
                    }
                }
                
                setLoading(false);
            } catch (error) {
                console.error("Erro ao carregar estado:", error);
                setError("Falha ao carregar dados. Por favor, tente novamente mais tarde.");
                setLoading(false);
            }
        };
        
        loadStateAfterRefresh();
        
        // Este efeito s deve executar uma vez ao montar o componente
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    // useEffect para lidar com pesquisas
    useEffect(() => {
        const handleSearch = async () => {
            // CORREO CRTICA: S executa se houver searchQuery OU se brandClicked for true
            // Isso evita executar no refresh normal da pgina
            const brandClicked = sessionStorage.getItem('brandClicked') === 'true';
            
            // Se no h searchQuery E no clicou no brand, NO FAZ NADA (mantm localStorage)
            if (!searchQuery && !brandClicked) {
                console.log("Sem ao necessria - mantendo estado atual");
                return;
            }
            
            if (
                !searchQuery ||
                (searchQuery.trim() === '' && brandClicked)
            ) {
                try {
                    setLoading(true);
                    // Limpar o sessionStorage aps uso
                    sessionStorage.removeItem(
                        'brandClicked'
                    );

                    // Limpar currentVideoId e lastSearchQuery do localStorage
                    localStorage.removeItem(
                        'currentVideoId'
                    );
                    
                    // NOVA ALTERAO: Limpar a pesquisa salva quando clica no brand
                    localStorage.removeItem(
                        'lastSearchQuery'
                    );
                    setLastSearchTerm('');

                    // obter videos populares
                    const videos =
                        await getPopularVideos(
                            15
                        );
                    setWatchLaterVideos(videos);
                    
                    //  Salvar os vdeos populares no localStorage para persistir aps refresh
                    localStorage.setItem('watchLaterVideos', JSON.stringify(videos));

                    // IMPORTANTE: Sempre carregar o primeiro vdeo aps clicar no brand
                    if (videos.length > 0) {
                        const videoDetails = await getVideoDetails(videos[0].id);
                        setCurrentVideo(videoDetails);
                        
                        // Salvar o novo estado no localStorage
                        localStorage.setItem('currentVideoDetails', JSON.stringify(videoDetails));
                        localStorage.setItem('currentVideoId', videoDetails.id);
                    }

                    setLoading(false);
                } catch (error) {
                    console.log(
                        'Failed to fetch videos. Please try again later.'
                    );
                    setError(error.message);
                    setLoading(false);
                }
                return;
            }

            // NOVA ALTERAO: Salvar a pesquisa atual no localStorage
            if (searchQuery && searchQuery.trim() !== '') {
                localStorage.setItem('lastSearchQuery', searchQuery);
                setLastSearchTerm(searchQuery);
            } else {
                // Se no tiver query nova, usar a salva (para manter persistncia)
                const savedSearch = localStorage.getItem('lastSearchQuery');
                if (savedSearch) {
                    setLastSearchTerm(savedSearch);
                }
            }
            
            // Cdigo para pesquisa
            try {
                setLoading(true);
                console.log(
                    'Searching for:',
                    searchQuery
                );

                // Buscar vdeos relacionados  pesquisa
                const searchResults =
                    await searchVideos(
                        searchQuery
                    );
                console.log(
                    'Search results:',
                    searchResults
                );

                if (
                    searchResults &&
                    searchResults.length > 0
                ) {
                    // Atualizar a lista de vdeos com os resultados da pesquisa
                    setWatchLaterVideos(
                        searchResults
                    );
                    
                    // SOLUO: Salvar os resultados de pesquisa no localStorage para persistir aps refresh
                    localStorage.setItem('watchLaterVideos', JSON.stringify(searchResults));

                    // Corrigindo a extrao do ID do vdeo
                    const firstVideoId =
                        getVideoIdFromResult(
                            searchResults[0]
                        );

                    if (firstVideoId) {
                        try {
                            // Definir o primeiro resultado como o vdeo atual
                            const videoDetails =
                                await getVideoDetails(
                                    firstVideoId
                                );
                            setCurrentVideo(
                                videoDetails
                            );

                            // CRTICO: Salvar TODOS os dados no localStorage para persistir aps refresh
                            localStorage.setItem(
                                'currentVideoId',
                                videoDetails.id
                            );
                            localStorage.setItem(
                                'currentVideoDetails',
                                JSON.stringify(videoDetails)
                            );
                        } catch (detailsError) {
                            console.error(
                                'Error fetching video details:',
                                detailsError
                            );
                            // Fallback: usar as informaes bsicas do vdeo
                            setCurrentVideo(
                                searchResults[0]
                            );
                        }
                    } else {
                        setError(
                            'Invalid video ID from search results'
                        );
                    }
                } else {
                    setError(
                        'No videos found for this search query'
                    );
                }

                setLoading(false);
            } catch (error) {
                console.error(
                    'Search error:',
                    error
                );
                setError(
                    'Failed to search videos. Please try again.'
                );
                setLoading(false);
            }
        };

        handleSearch();
    }, [searchQuery]);

    // Funao para extrair o ID do video de diferentes formatos de resposta
    const getVideoIdFromResult = videoResult => {
        // Verificar se o ID est na estrutura aninhada (comum para resultados de pesquisa)
        if (
            videoResult.id &&
            videoResult.id.videoId
        ) {
            console.log(
                'Found videoId in id.videoId:',
                videoResult.id.videoId
            );
            return videoResult.id.videoId;
        }

        // Verificar se o ID est diretamente disponvel (comum para vdeos populares)
        if (
            videoResult.id &&
            typeof videoResult.id === 'string'
        ) {
            console.log(
                'Found direct id:',
                videoResult.id
            );
            return videoResult.id;
        }

        // Para outros formatos de resposta que podem surgir
        if (
            videoResult.snippet &&
            videoResult.snippet.resourceId &&
            videoResult.snippet.resourceId.videoId
        ) {
            console.log(
                'Found videoId in snippet.resourceId:',
                videoResult.snippet.resourceId
                    .videoId
            );
            return videoResult.snippet.resourceId
                .videoId;
        }

        console.error(
            'Could not extract video ID from:',
            videoResult
        );
        return null;
    };

    // funao para definir um novo video como o video atual
    const handleSelectVideo = async videoId => {
        try {
            setLoading(true);

            // Salvar o ID do vdeo selecionado no localStorage
            localStorage.setItem('currentVideoId', videoId);

            const videoDetails = await getVideoDetails(videoId);
            setCurrentVideo(videoDetails);
            
            // IMPORTANTE: Salvar tambm o vdeo atual completo no localStorage
            // para garantir que ele persista exatamente igual aps o refresh
            try {
                localStorage.setItem('currentVideoDetails', JSON.stringify(videoDetails));
                
                // MUITO IMPORTANTE: Salvar tambm a lista de vdeos atual
                // para garantir que ela persista exatamente igual aps o refresh
                localStorage.setItem('watchLaterVideos', JSON.stringify(watchLaterVideos));
            } catch (err) {
                console.error('Erro ao salvar estado:', err);
            }
            
            setLoading(false);
        } catch (error) {
            console.log(
                'Failed to fetch video details. Please try again later.'
            );
            setError(error.message);
            setLoading(false);
        }
    };

    // funao para compartilhar o video atual
    const handleShareVideo = videoId => {
        const shareUrl = `https://www.youtube.com/watch?v=${videoId}`;

        if (navigator.share) {
            navigator
                .share({
                    title: currentVideo.snippet
                        ?.title,
                    text: 'Confira este vdeo!',
                    url: shareUrl
                })
                .catch(error =>
                    console.log(
                        'Erro ao compartilhar:',
                        error
                    )
                );
        } else {
            // Fallback - copiar para rea de transferncia
            navigator.clipboard
                .writeText(shareUrl)
                .then(() =>
                    alert(
                        'Link copiado para a rea de transferncia!'
                    )
                )
                .catch(err =>
                    console.error(
                        'Erro ao copiar link:',
                        err
                    )
                );
        }
    };

    // Funao para formatar a duraao do video
    const formatDuration = duration => {
        // A duraao vem no formato ISO 8601, ex: PT1H2M10S

        if (!duration) return '0:00';

        let match = duration.match(
            /PT(\d+H)?(\d+M)?(\d+S)?/
        );

        if (!match) return '0:00';

        let hours = (match[1] || '').replace(
            'H',
            ''
        );
        let minutes = (match[2] || '').replace(
            'M',
            ''
        );
        let seconds = (match[3] || '').replace(
            'S',
            ''
        );

        // preencher com zerosa a esquerda
        if (hours) {
            hours = hours.padStart(2, '0');
        }
        minutes =
            minutes.padStart(2, '0') || '00';
        seconds =
            seconds.padStart(2, '0') || '00';

        return hours
            ? `${hours}:${minutes}:${seconds}`
            : `${minutes}:${seconds}`;
    };

    // funao para o loading
    if (loading) {
        return (
            <>
                <ContainerLoading className="container-loading">
                    <Loading
                        $speedborder="0.7"
                        $fontsize="8"
                        $size="1"
                    />
                </ContainerLoading>
            </>
        );
    }

    if (error) {
        return (
            <>
                <ContainerError className="container-error">
                    <ErrorCompoment
                        errmessage={error}
                    />
                </ContainerError>
            </>
        );
    }

    return (
        <>
            <StyleMain className="style-main">
                <LeftCol className="leftCol">
                    {currentVideo && (
                        <>
                            <VideoDetails
                                video={
                                    currentVideo
                                }
                                onShareVideo={
                                    handleShareVideo
                                }
                            />
                        </>
                    )}
                </LeftCol>

                <RightCol className="rightCol">
                    <WatchLaterTitle className="watch-later-title">
                        {searchQuery || lastSearchTerm
                            ? `Search results for: ${searchQuery || lastSearchTerm}`
                            : 'Watch Later'}
                    </WatchLaterTitle>

                    {watchLaterVideos.map(
                        video => {
                            const videoId =
                                video.id
                                    ?.videoId ||
                                video.id;
                            return (
                                <VideoList
                                    key={videoId}
                                    video={video}
                                    onVideoSelect={
                                        handleSelectVideo
                                    }
                                    formatDuration={
                                        formatDuration
                                    }
                                />
                            );
                        }
                    )}
                </RightCol>
            </StyleMain>
        </>
    );
};

Main.propTypes = {};

export default Main;
