import Youtube from './Youtube';

// Buscar vdeos com base em uma consulta
export const searchVideos = async (
    searchTerm,
    maxResults = 10
) => {
    try {
        const response = await Youtube.get(
            '/search',
            {
                params: {
                    part: 'snippet',
                    maxResults: maxResults,
                    q: searchTerm,
                    type: 'video' // Para garantir que s retorne vdeos, no playlists ou canais
                }
            }
        );

        if (
            response.data.items &&
            response.data.items.length > 0
        ) {
            // Extrair os IDs dos vdeos para buscar detalhes adicionais
            const videoIds = response.data.items
                .map(item => item.id.videoId)
                .join(',');

            // Fazer uma segunda chamada para obter detalhes dos vdeos (incluindo durao)
            const detailsResponse =
                await Youtube.get('/videos', {
                    params: {
                        part: 'snippet,contentDetails,statistics',
                        id: videoIds
                    }
                });

            // Se conseguimos obter detalhes, retornar os detalhes completos
            if (
                detailsResponse.data.items &&
                detailsResponse.data.items
                    .length > 0
            ) {
                return detailsResponse.data.items;
            }

            // Se no conseguimos obter detalhes, retornar os resultados originais
            return response.data.items;
        }

        return [];
    } catch (error) {
        console.error(
            'Error searching videos:',
            error
        );
        throw error;
    }
};

//buscar detalhes de um video especifico pelo ID
export const getVideoDetails = async videoId => {
    try {
        const response = await Youtube.get(
            '/videos',
            {
                params: {
                    part: 'snippet,contentDetails,statistics',
                    id: videoId
                }
            }
        );
        return response.data.items[0];
    } catch (error) {
        console.error(
            'Error fetching video details:',
            error
        );
        throw error;
    }
};

// obter videos populares (para a pagina inicial)
export const getPopularVideos = async (
    maxResults = 15
) => {
    try {
        const response = await Youtube.get(
            '/videos',
            {
                params: {
                    part: 'snippet,contentDetails,statistics',
                    chart: 'mostPopular',
                    maxResults
                }
            }
        );
        return response.data.items;
    } catch (error) {
        console.error(
            'Error fetching popular videos:',
            error
        );
        throw error;
    }
};
